<template>
  <div class="container">
    <form class="form" onsubmit="event.preventDefault();">
      <div class="form__header">
        Login
      </div>
      <div class="form__body">
        <p class="info__block">
          Please enter your email address and password to access the
          application.
        </p>
        <p class="error__block">{{ error }}</p>
        <div class="input__group">
          <label for="email">E-mail</label>
          <input type="text" v-model="email" id="email" />
        </div>
        <div class="input__group">
          <label for="password">Password</label>
          <input type="password" v-model="password" id="password" />
        </div>
        <div class="button__box">
          <button class="button__go" v-on:click="login">Login ></button>
        </div>
      </div>
      <div class="form__footer">Welcom to the Umbrella</div>
    </form>
  </div>
</template>

<style lang="scss" scoped>
.container {
  position: relative;
  display: flex;
  min-height: 100vh;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;

  background: rgb(243, 243, 243);

  .form {
    height: fit-content;
    padding: 20px 50px;
    max-width: 300px;
    position: relative;

    background: white;
    border: 1px solid rgb(233, 233, 233);
    border-radius: 0.2rem;

    &__header {
      padding-top: 10px;

      font-weight: 700;
      font-size: 1.2rem;
      text-transform: uppercase;
    }

    &__body {
      padding-top: 10px;

      .info__block {
        font-weight: 300;
        font-size: 0.8rem;
        padding-bottom: 10px;
      }

      .error__block {
        font-weight: 300;
        font-size: 0.8rem;
        padding-bottom: 10px;
        color: rgb(199, 25, 25);
      }

      .input__group {
        padding-top: 10px;
        padding-bottom: 10px;
        display: flex;
        flex-direction: column;

        label {
          font-weight: 300;
          font-size: 0.8rem;
        }

        input {
          padding: 5px 0;
          border: none;
          border-bottom: 1px solid black;
          outline: none;
        }
      }

      .button__box {
        display: flex;
        justify-content: flex-end;

        .button__go {
          background: transparent;
          font-size: 1rem;
          padding-top: 10px;
          border: none;
          outline: none;
        }
      }
    }

    &__footer {
      padding-top: 40px;

      font-weight: 300;
      font-size: 0.7rem;
      color: rgb(112, 9, 9);
      text-align: center;
    }
  }
}
</style>

<script>
import axios from "axios";

export default {
  name: "Login",
  data: function() {
    return {
      email: "",
      password: "",
      error: "",
    };
  },
  methods: {
    login: function() {
      if (!this.email || !this.password) {
        this.error = "Fill in all the fields";
        return 0;
      }
      axios
        .post("https://authdiplom.herokuapp.com/api/auth/login", {
          email: this.email,
          password: this.password,
        })
        .then((res) => {
          if (res.data.token) localStorage.jwt = res.data.token;
          else this.error = res.data.error.message;
          console.log(localStorage.jwt);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
};
</script>
